const Html = () => {
  const data = useStaticQuery(graphql`
  query newClientForm{ 
      site {  
        siteMetadata{ 
            forms {  
              clienteNuevo 
            }
          } 
      }
    }
  `) 
   console.log(data);
  return (
    <Layout> 
     <WaveHeader fill="#00000"/>
     saddasd
     <GoogleForm src={data.site.siteMetadata.forms.clienteNuevo}/>
    </Layout>
  )
}

export default Html
 
